.profile {    
    width: 300px;
    height: 100%;
    margin-top: 30px;
    border: 1px solid rgba(97, 95, 95, 0.3);
    border-radius: 5px;
    box-shadow: 2px 2px 2px 0px rgba(0, 0, 0, 0.18), 2px 1px 2px 0px rgba(0, 0, 0, 0.08), 2px 2px 2px 0px rgba(0, 0, 0, 0.18), 2px 2px 2px 0px rgba(0, 0, 0, 0.18);
}

.description{
    background-color: white;
    display: flex;
    align-items: center; 
    flex-direction: column;
}

.avatar{
    margin-top: 50px;
    display: flex;
    align-content: center;
    width: 80px;
    border-radius: 50%;  
}

.name{
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 0px;
}

.tag{
    font-size: 14px;
    font-weight: 500;
    margin-top: 8px;
    margin-bottom: 8px;
    color: rgb(112, 115, 117);
}

.location{
    margin-top: 0px;
    margin-bottom: 40px;
    color: rgb(112, 115, 117);
}

.stats{
    display: flex;
    
    justify-content: center;  
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0px;
    list-style: none;
    background-color: rgb(244, 246, 247);
    border-top: 1px solid rgb(182, 185, 185);
}  

.statsSection{
    display: flex;
    flex-basis: calc(100% / 3 );
    margin: 0px;
    padding: 0px;
    align-items: center;
    flex-direction: column;   
    border-right: 1px solid rgb(182, 185, 185);
}
.statsSection:last-child {
    border-right:none;
  }
.label{
    font-size: 12px;
    font-weight: 400;
    margin-top: 20px;
    align-items: center;    
}

.quantity{
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
}
